class projectmanage extends DataBase {
  complete() {
    this.changeAll(this.model.condition)
    this.addli(this.model.condition)
      // console.log(window.location.hash,9999)
      this.model.tableconfig.tablewidth = ES.selctorDoc('.projectmanage').box().clientWidth - 140
    this.loadlist('group')
    this.projectType = window.location.hash.indexOf('projectmanage')!==-1?1:3
    this.dom.find('.createbtn').on('click', () => {
      this.app.session.set('ischanged', false);
      if(this.projectType == 1){
        this.app.changePage('createproone', { type: 'new' })
      }else{
        this.app.changePage('createbackflowpro1', { type: 'new' })
      }
    })
    this.dom.find('.searchbtn').on('click', () => {
      this.model.condition.forEach((val) => {
        if (val[0].name == 'inspectTime') {
          this.model.apiData.startTime = this.getfiltercondition()[val[0].name][0].startTime
          this.model.apiData.endTime = this.getfiltercondition()[val[0].name][0].endTime
        } else {
          this.model.apiData[val[0].name] = this.getfiltercondition()[val[0].name].toString() == '' ? null : this.getfiltercondition()[val[0].name].toString()
        }
      })
      this.model.apiData.projectIdList = this.model.apiData.projectIdList ? this.model.apiData.projectIdList.split(',') : null
      this.model.apiData.page = 1;
      this.search(true)
    })
  }
  async search(bool) {
    let data2 = []
    this.tablecont.showloading()
      this.model.apiData.type = this.projectType
    let res = await this.api.queryproject(this.model.apiData)
    if (res.code == 0) {
      if (res.data.list.length > 0) {
        res.data.list.forEach((val) => {
          for (let i in val) {
            val[i] = val[i] == null ? '' : val[i]
          }
          let obj = {}
          let temp = val.quotedByAuditProject || val.taskInProgressCount > 0 ? 2 : 1
          obj.id = val.id + ',' + val.taskInProgressCount + ',' + temp;
          switch (val.status) {
            case 1:
              val.status = '未启用';
              obj.operation = this.model.listicon.action;
              break;
            case 2:
              val.status = '已启用';
              obj.operation = this.model.listicon.action1;
              break;
          }
          Tool.configxlkformat(this.app.constmap['MODALITY']).forEach(function (a, b) {
            if (a.idx == val.modality) {
              val.modality = a.val;
            }
          })
          data2.push(obj)
          val.createTime = Tool.time(val.createTime, 'yyyy-mm-dd')
        })
        this.tablecont.setData(res.data.list, data2)
      } else {
        this.tablecont.noData();
      }
    }
    if (bool) {
      this.tablecont.getTotal(res.data.pages, 2, res.data.total)
    }
    this.initscroll('scrolltable')
  }
  async listaction(value) {
    switch (value.classname) {
      case 'view':
        if(this.projectType === 1){
          window.open('http://' + window.location.host + '/#!/createproone/view/' + value.id.split(',')[0])
          // this.app.changePage('createproone', { type: 'view', projectid: value.id.split(',')[0] })
        }else{
          window.open('http://' + window.location.host + '/#!/createbackflowpro1/view/' + value.id.split(',')[0])
          // this.app.changePage('createbackflowpro1', { type: 'view', projectid: value.id.split(',')[0] })
        }
        break;
      case 'edit':
        if(this.projectType === 1){
          window.open('http://' + window.location.host + '/#!/createproone/edit/' + value.id.split(',')[0]+'/'+value.id.split(',')[2])
          // this.app.changePage('createproone',  { type: 'edit', projectid: value.id.split(',')[0], status: value.id.split(',')[2] })
        }else{
          window.open('http://' + window.location.host + '/#!/createbackflowpro1/edit/' + value.id.split(',')[0]+'/'+value.id.split(',')[2])
          // this.app.changePage('createbackflowpro1',  { type: 'edit', projectid: value.id.split(',')[0], status: value.id.split(',')[2] })
        }
        break;
      case 'copy':
        this.app.alert.show({
          title: ' ',
          msg: '确认复制吗？',
          close: true,
          sure: async ()=>{
            let copyRes = await this.api.project_clone({projectId:value.id.split(',')[0]})
            if(copyRes.code == 0){
              this.model.apiData.page = 1;
              this.search(true)
            }else{
              this.app.alert.show({
                title: ' ',
                msg: res.msg,
                close: true
              })
            }
          }
        })

        break;
      case 'open':
        let json = {
          id: parseInt(value.id.split(',')[0]),
          status: 2
        }
        this.app.loading.show()
        let res = await this.api.startproject(json)
        this.app.loading.hide()
        if (res.code == 0) {
          this.model.apiData.page = 1;
          this.search(true);
        } else {
          this.app.alert.show({
            title: ' ',
            msg: res.msg,
            close: true
          })
        }
        break;
      case 'stop':
        if (value.id.split(',')[1] > 0) {
          this.app.alert.show({
            title: ' ',
            msg: '有标注任务已引用此项目，请先把相关任务暂停后再进行此操作。',
            close: true
          })
        } else {
          this.app.alert.show({
            title: ' ',
            msg: '确定需要暂停启用项目？',
            close: true,
            sure: async () => {
              let json = {
                id: parseInt(value.id.split(',')[0]),
                status: 1
              }
              this.app.loading.show()
              let res = await this.api.startproject(json)
              this.app.loading.hide()
                if (res.code == 0) {
                  this.model.apiData.page = 1;
                  this.search(true);
                } else {
                  this.app.alert.show({
                    title: ' ',
                    msg: res.msg,
                    close: true
                  })
                }
            }
          })
        }
        break;
    }

  }
  toggletab(value){
    this.app.changePage(this.projectType==1?'projectmanage':'backflowproject', { type: value.id })
  }
  getapidata(value) {
    if (value) {
      switch (value.name) {
        case 'projectIdList':
          this.chooseData.moduleobj.projectIdList.loading(true)
          this.project_name_search(value.data.data ? value.data.data.trim() : "");
          break;
      }
    } else {
      this.chooseData.moduleobj.projectIdList.loading(true)
      this.project_name_search('');
    }
  }
  async project_name_search(projectName) {
    let {data} = await this.api.project_name_search({projectName,type:this.projectType})
    this.chooseData.moduleobj.projectIdList.loading(false)
    let temparr = data.list.map((val) => {
      return {
        idx:val.projectId,
        val:val.projectName
      }
    })
    this.chooseData.renderHtml(temparr, 'projectIdList')
  }
  resize(){
    let ch = ES.selctorDoc(window).box().clientHeight - 100
    let cw = ES.selctorDoc(window).box().clientWidth - 240
    ES.selctorDoc('.projectmanage').css({ 'height': ch, 'width': cw });
    ES.selctorDoc('.projecttable .maintable').css({ 'width': cw - 100 });
    ES.selctorDoc('.projectmanage .table').css({ 'height': ch, 'width': cw });
    ES.selctorDoc('.scrolltable').css({ 'height': ch - 260 });
  }

}
module.exports = projectmanage;



